<template>
    <section class="pc-container pb-4">
      <div class="pcoded-content">
        <div class="row mb-4">
          <div class="col-md-auto">
            <button type="button" class="btn btn-outline-primary border-1"
              :class="{ 'btn-primary text-white': isCurrentRoute('civil') }"
              @click="registroCivilLaboral">
              Reporte Civil Laboral
            </button>
          </div>
          <div class="col-md-auto">
            <button type="button" class="btn btn-outline-primary border-1"
              :class="{ 'btn btn-primary text-white': isCurrentRoute('penal') }"
              @click="registroPenal">
            Reporte   Penal
            </button>
          </div>
          <div class="col-md-auto">
            <button type="button" class="btn btn-outline-primary border-1"
              :class="{ 'btn btn-primary text-white': isCurrentRoute('arbitral') }"
              @click="registroArbitral">
            Reporte  Arbitral
            </button>
          </div>
          <div class="col-md-auto">
            <button type="button" class="btn btn-outline-primary border-1"
              :class="{ 'btn btn-primary text-white': isCurrentRoute('indecopi') }"
              @click="registroIndecopi">
           Reporte   Indecopi
            </button>
          </div>
        </div>
        <civil v-if="page=='civil'"/>
        <penal v-if="page=='penal'"/>
        <arbitral v-if="page=='arbitral'"/>
        <indecopi v-if="page=='indecopi'"/>
      </div>
      <div>
    </div>

</section>
</template>
    
  <script>
  import axios from 'axios';
import civil from '@/modules/legal/datos/reportes/civil.vue';
import penal from '@/modules/legal/datos/reportes/penal.vue';
import arbitral from '@/modules/legal/datos/reportes/arbitral.vue';
import indecopi from '@/modules/legal/datos/reportes/indecopi.vue';
  export default {
    name: 'Registro',
    components: {
        civil,
        penal,
        arbitral,
        indecopi
    },
    data() {
        return {
         page:'civil',

        }},
    methods: {
      registroCivilLaboral() {
        this.page='civil';
      },
      registroPenal() {
        this.page='penal';
      },
      registroArbitral() {
        this.page='arbitral';
      },
      registroIndecopi() {
        this.page='indecopi';
      },
      isCurrentRoute(route) {
      return this.page === route;
      },
    },
  };
  </script>
